import { useEffect, useState } from 'react'
import { Autocomplete, Row, Select } from 'react-materialize'
import { useSelector, useDispatch } from 'react-redux'

// Componentes
import Table from '../../components/Table'
import AddButton from '../../components/AddButton'
import DesenvolvedorRowTable from '../../components/Desenvolvedor/DesenvolvedorRowTable'

// Ferramentas
import { fetchAllDevs } from '../../store/features/desenvolvedores/fetchAllDevs'
import { ordenacaoByItem } from '../../services/tools/ordenacaoByItem'
import { searchByString } from '../../services/tools/searchByString'


const getNameDevs = data => {
    if (data.length === 0) return {}

    // Devs final list
    const devs = {}

    data.map(
        (item, index) => {
            devs[item.nome] = null
        }
    )

    return devs
}

// Funo principal
const Desenvolvedor = () => {
    // Estados fundamentais
    const dispatch = useDispatch()
    const desenvolvedoresResponse = useSelector(state => state.desenvolvedores)
    const [data, setData] = useState(desenvolvedoresResponse.value.data)

    // Estados proprios da tela
    const [desenvolvedores, setDesenvovledores] = useState([])
    const [devNameList, setDevNameList] = useState({})
    const [searchNome, setSearchNome] = useState("")
    const [selectOrdenacao, setSelectOrdenacao] = useState("nenhuma")
    const [selectFormaOrndenacao, setSelectFormaOrndenacao] = useState("crescente")


    // Efects de conexo ao banco de dados
    useEffect(
        () => {
            Array(4).fill(0).map(() => dispatch(fetchAllDevs()))
        },
        [window.location.pathname]
    )

    useEffect(
        () => {
            dispatch(fetchAllDevs())
        },
        []
    )

    useEffect(
        () => setData(desenvolvedoresResponse.value.data),
        [desenvolvedoresResponse]
    )

    useEffect(
        () => {
            setDesenvovledores(data)
            setDevNameList(
                getNameDevs(data)
            )

        },
        [data, window.location.pathname, desenvolvedoresResponse]
    )

    // Ordeno da lista por topico em ordem crescente ou decrescente
    useEffect(
        () => {
            setDesenvovledores(ordenacaoByItem(data, selectOrdenacao, selectFormaOrndenacao))
        },
        [selectOrdenacao, selectFormaOrndenacao]
    )

    // Ordenao da lista de acordo com a barra de pesquisa de nomes
    useEffect(
        () => {
            setDesenvovledores(searchByString(data, searchNome, "nome"))
        },
        [searchNome]
    )

    return (
        <div>
            <div className="container">
                <Row>
                    <h2>Desenvolvedores <AddButton /></h2>
                </Row>
                <Row>
                    <Autocomplete
                        id="search-dev"
                        options={{
                            data: devNameList
                        }}
                        placeholder="Escreva aqui"
                        title='Procurar desenvolvedor'
                        s={8}
                        onChange={
                            e => {
                                setSearchNome(e.target.value)
                            }
                        }
                    />
                    <Select
                        id="select-nivel"
                        multiple={false}
                        label='Escolha ordenao'
                        onChange={e => {
                            setSelectOrdenacao(e.target.value)
                        }}
                        options={{
                            classes: '',
                            dropdownOptions: {
                                alignment: 'left',
                                autoTrigger: true,
                                closeOnClick: true,
                                constrainWidth: true,
                                coverTrigger: true,
                                hover: false,
                                inDuration: 150,
                                onCloseEnd: null,
                                onCloseStart: null,
                                onOpenEnd: null,
                                onOpenStart: null,
                                outDuration: 250
                            }
                        }}
                        s={2}
                    >
                        <option
                            disabled
                            value=""
                        >
                            Escolha sua opo
                        </option>
                        <option value="none">
                            Nenhuma
                        </option>
                        <option value="nome">
                            Nome
                        </option>
                        <option value="Nivel.nivel">
                            Nivel
                        </option>
                    </Select>
                    <Select
                        id="select-ordem"
                        multiple={false}
                        label='Escolha a forma'
                        onChange={e => {
                            setSelectFormaOrndenacao(e.target.value)
                        }}
                        options={{
                            classes: '',
                            dropdownOptions: {
                                alignment: 'left',
                                autoTrigger: true,
                                closeOnClick: true,
                                constrainWidth: true,
                                coverTrigger: true,
                                hover: false,
                                inDuration: 150,
                                onCloseEnd: null,
                                onCloseStart: null,
                                onOpenEnd: null,
                                onOpenStart: null,
                                outDuration: 250
                            }
                        }}
                        s={2}
                    >
                        <option value="crescente">
                            Crescente
                        </option>
                        <option value="decrescente">
                            Decrescente
                        </option>
                    </Select>
                </Row>

                <Table
                    heads={["Nome", "Nivel"]}>
                    <DesenvolvedorRowTable data={desenvolvedores} />
                </Table>
            </div>
            
        </div>
    )
}



export default Desenvolvedor
